.hero {
  background-color: var(--secondary-background-color);
  height: 502px;
  width: 100%;

  @media screen and (min-width: $tablet) {
    height: 416px;
  }

  @media screen and (min-width: $desktop) {
    height: 664px;
  }
}

.hero__container {
  position: relative;
  padding-top: 194px;
  width: 320px;
  height: 502px;
  overflow: hidden;

  @media screen and (min-width: $tablet) {
    width: 768px;
    height: 416px;
    padding-top: 104px;
  }
  @media screen and (min-width: $desktop) {
    width: 1200px;
    height: 664px;
    padding-top: 147px;
  }
}
// Big round image - Start

.hero__round {
  position: absolute;
  top: 83px;
  left: 36px;
  width: 346px;
  height: 344px;
  border-radius: 50%;
  background-color: var(--third-text-color);
  overflow: hidden;

  @media screen and (min-width: $tablet) {
    width: 335px;
    height: 333px;
    top: 43px;
    left: 248px;
  }
  @media screen and (min-width: $desktop) {
    width: 534px;
    height: 534px;
    top: 54px;
    left: 516px;
  }
}

// Big round image - End

// Ice-cream image - Start

.hero__main-img {
  position: absolute;
  top: 92px;
  left: 117px;
  width: 171px;
  height: 410px;
  background-image: url('../images/hero/hero-icecream-mob.png');
  background-size: 171px 410px;

  @media screen and (min-width: $tablet) {
    top: 80px;
    left: 332px;
    width: 143px;
    height: 336px;
    background-image: url('../images/hero/hero-icecream-tab.png');
    background-size: 143px 336px;
  }
  @media screen and (min-width: $desktop) {
    top: 108px;
    left: 636px;
    width: 236px;
    height: 557px;
    background-image: url('../images/hero/hero-icecream-desc.png');
    background-size: 236px 557px;
  }
}
/* Retina 2x */
@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .hero__main-img {
    background-image: url('../images/hero/hero-icecream-mob@2x.png');

    @media screen and (min-width: $tablet) {
      background-image: url('../images/hero/hero-icecream-tab@2x.png');
    }
    @media screen and (min-width: $desktop) {
      background-image: url('../images/hero/hero-icecream-desc@2x.png');
    }
  }
}
/* Retina 3x */
@media (min-device-pixel-ratio: 3),
  (min-resolution: 288dpi),
  (min-resolution: 3dppx) {
  .hero__main-img {
    background-image: url('../images/hero/hero-icecream-mob@3x.png');

    @media screen and (min-width: $tablet) {
      background-image: url('../images/hero/hero-icecream-tab@3x.png');
    }
    @media screen and (min-width: $desktop) {
      background-image: url('../images/hero/hero-icecream-desc@3x.png');
    }
  }
}
// Ice-cream image - End

// Milk image - Start

.hero__milk {
  position: absolute;
  top: 246px;
  left: 32px;
  width: 152px;
  height: 154px;
}
.hero__milk-img {
