  position: absolute;
  top: 265px;
  left: 546px;
  width: 190px;
  height: 150px;
  background-image: url('../images/hero/hero-girl-tab.png');
  background-size: 190px 150px;

  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: $desktop) {
    top: 423px;
    left: 877px;
    width: 304px;
    height: 241px;
    background-image: url('../images/hero/hero-girl-desc.png');
    background-size: 304px 241px;
  }
