/* Retina 2x */
@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .hero__girl-img {
    background-image: url('../images/hero/hero-girl-tab@2x.png');

    @media screen and (min-width: $desktop) {
      background-image: url('../images/hero/hero-girl-desc@2x.png');
    }
  }
}
/* Retina 3x */
@media (min-device-pixel-ratio: 3),
  (min-resolution: 288dpi),
  (min-resolution: 3dppx) {
  .hero__girl-img {
    background-image: url('../images/hero/hero-girl-tab@3x.png');

    @media screen and (min-width: $desktop) {
      background-image: url('../images/hero/hero-girl-desc@3x.png');
    }
