// Girl image - End

// Numbers

.hero__wrapper {
  position: absolute;
  top: 92px;
  left: 685px;
  width: 51px;
  height: 103px;

  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: $desktop) {
    top: 152px;
    left: 1092px;

    width: 92px;
    height: 168px;
  }
}
.hero__wrapper-16 {
  width: 43px;
  height: 44px;
  margin-left: 8px;

  @media screen and (min-width: $desktop) {
    width: 72px;
    height: 77px;
    margin-left: 20px;
  }

  &::after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    right: 0;

    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: var(--main-text-color);

    @media screen and (min-width: $desktop) {
      width: 30px;
      height: 30px;
    }
  }
}

.hero__wrapper-23 {
  position: relative;
  width: 51px;
  height: 43px;

  @media screen and (min-width: $desktop) {
    width: 92px;
    height: 76px;
  }

  &::after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    right: 0;

    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: var(--main-text-color);

    @media screen and (min-width: $desktop) {
      width: 30px;
      height: 30px;
    }
  }
}

.hero__number {
  position: relative;
  z-index: 100;
  display: block;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: calc(32 / 28);
  margin-bottom: 0;
  text-align: left;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--secondary-text-color);

  @media screen and (min-width: $desktop) {
    font-size: 46px;
    line-height: calc(53 / 46);
    margin-bottom: 2px;
  }
}

.hero__number-23 {
  text-align: center;
}

.hero__number-desc {
  display: block;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: calc(10 / 8);
  text-align: left;
  color: var(--main-text-color);

  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: calc(18 / 14);
  }
}
.cafes {
  text-align: center;
}
// H1 - Start

.hero__content {
  position: relative;
  width: 218px;
  height: auto;
  margin-bottom: 61px;
  @media screen and (min-width: $tablet) {
    width: 162px;
  }

  @media screen and (min-width: $desktop) {
    width: 277px;
    margin-bottom: 108px;
  }

  &:hover {
    margin-bottom: 35px;
  }
}


.hero__header {
  width: 100%;
  height: 106px;
  margin-bottom: 20px;

  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: calc(30 / 26);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--main-text-color);

  transition-duration: 500ms;

  @media screen and (min-width: $tablet) {
    height: 80px;
    font-size: 22px;
    line-height: calc(25 / 22);
  }
  @media screen and (min-width: $desktop) {
    height: 137px;
    font-size: 38px;
    line-height: calc(44 / 38);
    margin-bottom: 24px;
  }
}

.hero__header-part {
  display: block;
  width: 100%;
  font-size: 48px;
  line-height: calc(46 / 48);
  color: var(--secondary-text-color);

  @media screen and (min-width: $tablet) {
    font-size: 34px;
    line-height: calc(31 / 34);
  }
  @media screen and (min-width: $desktop) {
    font-size: 58px;
    line-height: calc(52 / 58);
  }
}
// H1 - End

// 2 Buttons - Start

.hero__buttons {
  @extend %list;
  display: flex;
  gap: 8px;
}

.hero__item {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 24px;
  border-radius: 20px;

  transition: background-color var(--animation-timing-function);

  @media screen and (min-width: $desktop) {
    height: 40px;
  }
  &:hover,
  &:focus {
    background-color: #ecebeb;
    box-shadow: 0px 3px 1px rgb(0 0 0 / 10%), 0px 1px 2px rgb(0 0 0 / 8%),
      0px 2px 2px rgb(0 0 0 / 12%);
    transform: scale(1.1);
    transition: transform var(--animation-timing-function);
  }
}
.hero__item:hover .hero__link {
  color: var(--secondary-text-color);
  font-weight: 700;
}
.hero__item:hover .hero__link-about {
  color: var(--main-text-color);
}
.hero__item-about:hover, .hero__item-about:focus {
background-color: var(--secondary-text-color);
}

.hero__item-products {
  width: 62.4px;
  background: var(--secondary-text-color);

  @media screen and (min-width: $desktop) {
    width: 107px;
  }
}

.hero__item-about {
  width: 82.8px;
  background: var(--main-text-color);

  @media screen and (min-width: $desktop) {
    width: 138px;
  }
}
.hero__link {
  @extend %link;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: calc(10 / 8);

  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: calc(18 / 14);
  }
}

.hero__link-products {
  color: var(--main-text-color);
}
.hero__link-about {
  color: var(--secondary-text-color);
}

// 2 Buttons - End

// Hero Text - Start
.hero__text {
  position: relative;
  max-width: 125px;
  height: 48px;
  margin-left: 152px;
  margin-bottom: 7px;

  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: calc(12 / 8);
  color: var(--main-text-color);

  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: $desktop) {
    max-width: 200px;
    height: 78px;
    font-size: 14px;
    line-height: calc(20 / 14);
    margin-left: 213px;
    margin-bottom: 20px;
  }
}

// Hero Text - End

// Hero Button (arrow right)

.hero__btn {
  width: 18px;
  height: 18px;
  margin-left: 152px;
  padding: 0;
  border-radius: 50%;
  border: 0;
  background-color: transparent;
  background-image: url('../images/hero/hero__arrow-right.png');
  background-size: cover;

  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: $desktop) {
    width: 26px;
    height: 26px;
    margin-left: 213px;
    background-image: url('../images/hero/hero__arrow-right__2x.png');

  }
  &:hover,
  &:focus {
    background-color: #ecebeb;
    box-shadow: 0px 3px 1px rgb(0 0 0 / 10%), 0px 1px 2px rgb(0 0 0 / 8%),
      0px 2px 2px rgb(0 0 0 / 12%);
    transform: scale(1.1);
    transition: transform var(--animation-timing-function);
  }
}

// Animation - hero content - Start

.hero__content:hover .hero__header {
  @media screen and (min-width: $tablet) {
  margin-left: 30px;
  width: 120%;
  height: auto;
  padding: 20px;
  margin-bottom: 30px;
  text-align: center;

  box-shadow: 0px 3px 1px rgb(0 0 0 / 10%), 0px 1px 2px rgb(0 0 0 / 8%),
    0px 2px 2px rgb(0 0 0 / 12%);
  transform: scale(1.1);
  transition: transform var(--animation-timing-function), scale var(--animation-timing-function);
}
}

.hero__content:hover .hero__buttons {
  gap: 40px;
  margin-left: 10px;
  width: 100%;

  @media screen and (min-width: $tablet) {
    margin-left: 30px;
    width: 120%;
    gap: 60px;

  }
}
.hero__content:hover .hero__item {
  box-shadow: 0px 3px 1px rgb(0 0 0 / 10%), 0px 1px 2px rgb(0 0 0 / 8%),
      0px 2px 2px rgb(0 0 0 / 12%);
    transform: scale(1.4);
    transition: transform var(--animation-timing-function);
}

.hero__content:hover .hero__item-products {
  transition-duration: 1000ms;
  width: 230px;
}
.hero__content:hover .hero__item-about {
  transition-duration: 2000ms;
  width: 280px;
}

