//  Animation Pulse
.pulse {  
  display: block;
margin-bottom: 16px;  
width: 50px;
  height: 50px;
  border-radius: 50%;
  background: transparent;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(245, 9, 127, 0.4);
  animation: pulse 2s infinite;

  @media screen and (min-width: $desktop) {
    margin-bottom: 15px;  
    width: 85px;
  height: 85px;
  }
.pulse:hover {
  animation: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgb(245, 95, 170);
  }
  70% {
      -webkit-box-shadow: 0 0 0 10px rgba(245, 32, 57, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(245, 32, 57, 0);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(245, 32, 57, 0.783);
    box-shadow: 0 0 0 0 rgba(245, 32, 57, 0.783);
  }
  70% {
      -moz-box-shadow: 0 0 0 30px rgba(245, 32, 57, 0);
      box-shadow: 0 0 0 30px rgba(245, 32, 57, 0);
  }
  100% {
      -moz-box-shadow: 0 0 0 0 rgba(245, 32, 57, 0);
      box-shadow: 0 0 0 0 rgba(245, 32, 57, 0);
  }
}
}